.modal-window {
  position: absolute;
  display: flex;
  flex-direction: column;
  max-width: 1000px;
  width: 70vw;
  height: auto;
  top: 50%;
  left: 50%;
  transform: translate(100%, -50%);
  border: none;
  border-radius: 10px;
  gap: 1rem;
  padding: 2rem;
  opacity: 0.5;
  background-color: #def2f1;
  transition: 0.5s;
}
@media (max-width: 600px) {
  .modal-window {
    width: 85vw;
  }
}
.modal-window ol {
  font-size: 1.2rem;
  font-weight: 700;
}
.modal-window ol li {
  font-size: 1rem;
  margin-top: 5px;
}

.close {
  position: absolute;
  right: 10px;
  top: 10px;
  cursor: pointer;
}

.modal-window_open {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 1;
  z-index: 15;
}

label {
  display: flex;
  justify-content: space-between;
  font-size: 1.2rem;
  font-weight: 700;
}
label select,
label input {
  width: 30%;
  height: 2rem;
  border-radius: 10px;
  border: none;
  padding: 0 10px 0 10px;
  font-size: 1rem;
  font-weight: normal;
}
label textarea {
  border-radius: 10px;
  border: none;
  padding: 10px;
  font-size: 1rem;
  font-weight: normal;
}
label select:hover,
label input:hover,
label textarea:hover {
  border: 1px solid #17252a;
}
label select:focus,
label input:focus,
label textarea:focus {
  outline: none;
  border: 2px solid #2b7a78;
}
label select:invalid,
label input:invalid,
label textarea:invalid {
  outline: none;
  border: 2px solid red;
}

input[type=color] {
  background-color: #fff;
  padding: 0 2px 0 2px;
}

.info-block {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.info-parts {
  padding-top: 20px;
}

.button-save,
.button-save-note {
  align-self: center;
}

.profile {
  display: flex;
  flex-direction: column;
}

.car-info {
  display: flex;
  justify-content: space-around;
  font-size: 1rem;
  font-weight: 700;
}

.car-img svg {
  width: 100%;
  height: 100%;
}

.profile-control-panel {
  display: flex;
  justify-content: space-around;
}

.note-list {
