  display: flex;
  flex-direction: column;
  overflow: auto;
  align-items: center;
  gap: 20px;
  margin-top: 40px;
  scrollbar-color: #17252a #fff; /* «цвет ползунка» «цвет полосы скроллбара» */
  scrollbar-width: thin; /* толщина */
}

.note-block {
  height: 100px;
  display: flex;
  align-items: center;
  border-radius: 10px;
  background-color: #fff;
  transition: 0.2s;
  margin: 1px 0 1px 0;
  cursor: pointer;
}
.note-block p {
  margin: 5px 0 5px 0;
}

.note-block.open-text {
  height: auto;
}

.info-note-list {
  height: 85%;
  overflow-y: hidden;
  width: 700px;
  padding: 0px 10px 20px 20px;
}
@media (max-width: 1100px) {
  .info-note-list {
    width: 500px;
  }
}
@media (max-width: 800px) {
  .info-note-list {
    width: 400px;
  }
}
@media (max-width: 600px) {
  .info-note-list {
    width: 280px;
  }
}
@media (max-width: 470px) {
  .info-note-list {
    width: auto;
  }
}

