.car-block:hover {
  border: 2px solid #17252a;
  transform: scale(1.02);
}

.car-list::-webkit-scrollbar {
  width: 10px;
}

.car-list::-webkit-scrollbar-track {
  background: #17252a;
}

.car-list::-webkit-scrollbar-thumb {
  background-color: #fff;
  border: 2px solid #17252a;
}

.info-car-list {
  max-width: 500px;
  display: flex;
  flex-wrap: wrap;
  gap: 0 20px;
  padding: 0 10px 0 20px;
  font-weight: 600;
}
.info-car-list p {
  margin: 10px 0 10px 0;
}

.control {
  display: flex;
  gap: 10px;
  padding: 0 10px 0 10px;
}
@media (max-width: 800px) {
  .control {
    flex-direction: column;
  }
}

.login-form {
  transform: translateY(50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.login-label {
  width: 300px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.login-label input {
  width: 200px;
}

.buttons-login-form {
  width: 300px;
  display: flex;
  justify-content: space-around;
  margin-top: 10px;
}

.info-login-form {
  color: red;
}

.loader {
  transform: rotateZ(45deg);
  perspective: 1000px;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  color: #fff;
  margin-top: 15vh;
}

.loader:before,
.loader:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: inherit;
  height: inherit;
  border-radius: 50%;
  transform: rotateX(70deg);
  animation: 1s spin linear infinite;
}

.loader:after {
  color: #ff3d00;
  transform: rotateY(70deg);
  animation-delay: 0.4s;
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%) rotateZ(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotateZ(360deg);
  }
}
@keyframes rotateccw {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(-360deg);
  }
}
@keyframes spin {
  0%, 100% {
    box-shadow: 0.2em 0px 0 0px currentcolor;
  }
  12% {
    box-shadow: 0.2em 0.2em 0 0 currentcolor;
  }
  25% {
    box-shadow: 0 0.2em 0 0px currentcolor;
  }
  37% {
    box-shadow: -0.2em 0.2em 0 0 currentcolor;
  }
  50% {
    box-shadow: -0.2em 0 0 0 currentcolor;
  }
  62% {
    box-shadow: -0.2em -0.2em 0 0 currentcolor;
  }
  75% {
    box-shadow: 0px -0.2em 0 0 currentcolor;
  }
  87% {
    box-shadow: 0.2em -0.2em 0 0 currentcolor;
  }
}
html {
  box-sizing: border-box;
}
@media (max-width: 470px) {
  html {
    font-size: 12px;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

#overlay {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: rgb(0, 0, 0);
  opacity: 0;
  z-index: -1;
  transition: 0.5s;
}

#overlay.active {
  display: block;
  opacity: 0.2;
  z-index: 10;
}

body {
  padding: 20px;
  max-width: 1200px;
  margin: auto;
  font-family: "Exo 2", sans-serif;
  background-color: aliceblue;
  color: #17252a;
  overflow: hidden;
}
@media (max-width: 470px) {
  body {
    padding: 10px;
  }
}

.btn {
  width: fit-content;
  height: 30px;
  background-color: #2b7a78;
  color: white;
  border: none;
  border-radius: 10px;
  padding: 0 15px 0 15px;
  transition: 0.5s;
  cursor: pointer;
}

.btn:hover {
  background-color: #17252a;
}

.btn:disabled {
  background-color: rgba(43, 122, 120, 0.3764705882);
  cursor: default;
}

.title {
  font-size: 24px;
  text-align: center;
}

.main-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-menu__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
}

.main-menu li {
  list-style: none;
}

.main-menu__link {
  display: block;
  padding: 10px 25px;
  font-weight: 600;
  text-decoration: none;
  transition: 0.5s;
  color: #17252a;
}
@media (max-width: 470px) {
  .main-menu__link {
    padding: 10px 10px;
  }
}

.main-menu__link:hover {
  text-decoration: none;
  background-color: #17252a;
  color: #fff;
}

.main-menu__link.active {
  text-decoration: none;
  background-color: #2b7a78;
  color: #fff;
}

.content {
  padding: 1.5em 0;
}

.add-auto {
  margin-bottom: 40px;
